import React, { useContext } from "react";
import "./publication.scss";
import { publicationSection } from "../../portfolio";
import { Fade } from "react-reveal";
import StyleContext from "../../contexts/StyleContext";

export default function Publication() {
  const { isDark } = useContext(StyleContext);

  if (!publicationSection.display) {
    return null;
  }

  return (
    <Fade bottom duration={1000} distance="20px">
      <div className="main publication-section" id="publications">
        <div className="publication-container">
          <h1 className="skills-heading">{publicationSection.title}</h1>
          <p
            className={
              isDark
                ? "dark-mode publication-subtitle"
                : "subTitle publication-subtitle"
            }
          >
            {publicationSection.subtitle}
          </p>

          <div className="publications-container">
            {publicationSection.publications.map((publication, i) => (
              <div
                key={i}
                className={
                  isDark
                    ? "dark-mode publication-card publication-card-dark"
                    : "publication-card publication-card-light"
                }
              >
                <div className="publication-detail">
                  <h5
                    className={
                      isDark
                        ? "dark-mode publication-title"
                        : "publication-title"
                    }
                  >
                    {publication.title}
                  </h5>
                  <p
                    className={
                      isDark
                        ? "dark-mode publication-conference"
                        : "publication-conference"
                    }
                  >
                    {publication.conference}
                  </p>
                  {publication.additionalInfo && (
                    <p
                      className={
                        isDark
                          ? "dark-mode publication-additional-info"
                          : "publication-additional-info"
                      }
                    >
                      {publication.additionalInfo}
                    </p>
                  )}
                  {publication.link && (
                    <a
                      href={publication.link}
                      target="_blank"
                      rel="noopener noreferrer"
                      className={
                        isDark
                          ? "dark-mode publication-link"
                          : "publication-link"
                      }
                    >
                      View Publication
                    </a>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </Fade>
  );
}
