import React from "react";
import "./Progress.scss";
import {techStack} from "../../portfolio";
import {Fade} from "react-reveal";

export default function StackProgress() {
  if (techStack.viewSkillBars) {
    // Divide skills into two sections (Software Development and Machine Learning)
    const half = Math.ceil(techStack.experience.length / 2);
    const leftSkills = techStack.experience.slice(0, half);
    const rightSkills = techStack.experience.slice(half);

    return (
      <Fade bottom duration={1000} distance="20px">
        <div className="skills-section">
          {/* Title Summarization */}
          <h1 className="skills-title">
            Bridging the Gap
          </h1>
          <p className="skills-subtitle">
            A balanced journey across robust software development and cutting-edge machine learning research.
          </p>
          <div className="skills-container">
            {/* Software Development Column */}
            <div className="skills-column">
              <h1 className="skills-heading">Software Development</h1>
              {leftSkills.map((exp, i) => (
                <div key={i} className="skill">
                  <div className="skill-header">
                    <p className="skill-name">{exp.Stack}</p>
                    <p className="progress-percentage">{exp.progressPercentage}</p>
                  </div>
                  <div className="meter">
                    <span style={{ width: exp.progressPercentage }}></span>
                  </div>
                  <p className="skill-description">{exp.description}</p>
                </div>
              ))}
            </div>

            {/* Machine Learning Column */}
            <div className="skills-column">
              <h1 className="skills-heading">Machine Learning</h1>
              {rightSkills.map((exp, i) => (
                <div key={i} className="skill">
                  <div className="skill-header">
                    <p className="skill-name">{exp.Stack}</p>
                    <p className="progress-percentage">{exp.progressPercentage}</p>
                  </div>
                  <div className="meter">
                    <span style={{ width: exp.progressPercentage }}></span>
                  </div>
                  <p className="skill-description">{exp.description}</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </Fade>
    );
  }
  return null;
}
